export default angular.module('deskworks.DeskworksSweepProduct', [
  'ngResource',
  'deskworks.config',
])

  .factory('DeskworksSweepProduct', function($resource, $timeout, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/deskworks-sweep/products/:productId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: ({ productId }) => res.get({ productId }).$promise,

      save: ({ productId, data }) => res.update({ productId }, data).$promise,
    };
  });
