import DeskworksSweepProduct from './deskworks-sweep-product.service';

/**
 * Membership Product -> Count Members / Co-members
 */
export default angular.module('deskworks.deskworksSweepProductDirective', [
  'debounce',
  DeskworksSweepProduct.name,
])

  .directive('deskworksSweepProduct', function() {
    const templateUrl = 'product/deskworks-sweep-product.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'DeskworksSweepProductCtrl as $ctrl',
      bindToController: {
        productId: '<',
        save: '=',
      },
    };
  })

  .controller('DeskworksSweepProductCtrl', function($q, dwAlerts, debounce, DeskworksSweepProduct) {
    const $ctrl = this;

    /**
     * $onInit
     */
    $ctrl.$onInit = () => {
      DeskworksSweepProduct.get({ productId: $ctrl.productId })
        .then(result => {
          $ctrl.product = result;
          $ctrl.save = save;
        })
        .catch(err => dwAlerts.error(err, 'Failed to get deskworks sweep product.'))
        .catch(angular.noop);
    };

    /**
     * Save settings
     */
    function save() {
      return DeskworksSweepProduct.save({ productId: $ctrl.productId, data: $ctrl.product })
        .catch(err => dwAlerts.error(err, 'Failed to save deskworks sweep product.'));
    }
  });
